class Store {
    static instance;
    reducerMap;
    stateMap;
    subscribeArray;
    constructor() {
        this.reducerMap = {};
        this.stateMap = {};
        this.subscribeArray = [];
    }
    static get singleInstance() {
        if (!this.instance) {
            this.instance = new Store();
        }
        return this.instance;
    }
    saveReducer(type, reducer) {
        this.reducerMap[type] = reducer;
    }
    getReducer(type) {
        return this.reducerMap[type];
    }
    saveState(scopeName, state) {
        this.stateMap[scopeName] = state;
    }
    getState(scopeName) {
        return this.stateMap[scopeName];
    }
    saveSubscribe(subscribe) {
        this.subscribeArray.push(subscribe);
    }
    get reducers() {
        return this.reducerMap;
    }
    get states() {
        return this.stateMap;
    }
    get subscribes() {
        return this.subscribeArray;
    }
}
var store = Store.singleInstance;

//  reducers 
//  createSlice  actions  reducer 
// ,  Action, 
function getProxyActions(scopeName, reducers) {
    const proxyReducers = {};
    for (const fnName in reducers) {
        const type = `${scopeName}/${fnName}`;
        store.saveReducer(type, reducers[fnName]);
        proxyReducers[fnName] = function (payload) {
            return {
                type,
                payload,
            };
        };
    }
    return proxyReducers;
}
function createSlice(config) {
    const { name, initialState, reducers } = config;
    if (store.getState(name)) {
        throw new Error(` ${name} `);
    }
    store.saveState(name, initialState);
    const actions = getProxyActions(name, reducers);
    const reducer = (state, action) => {
        if (state === void 0) {
            state = store.getState(name);
        }
        const reducerFn = store.getReducer(action.type);
        return reducerFn && typeof reducerFn === 'function'
            ? {
                ...state,
                ...reducerFn(state, action),
            }
            : state;
    };
    return {
        actions,
        reducer,
    };
}

function subscribe(fn) {
    store.saveSubscribe(fn);
}

function useDispatch() {
    if (Object.keys(store.states).length === 0) {
        throw new Error(': createSlice is undefined');
    }
    const dispatch = (action) => {
        const { type } = action;
        const scopeName = type.split('/')[0];
        const reducer = store.getReducer(type);
        if (!reducer || typeof reducer !== 'function') {
            throw new Error('reducer is undefined');
        }
        const prevState = store.getState(scopeName);
        const newState = reducer(prevState, action);
        store.saveState(scopeName, {
            ...prevState,
            ...newState,
        });
        // 
        store.subscribes.forEach((fn) => fn());
    };
    return dispatch;
}

function useSelector(selector) {
    const state = selector(store.states);
    return state;
}

export { createSlice, subscribe, useDispatch, useSelector };
